import { useState, useEffect } from "react";

const dietPlans = [
    {
        title: "1     ",
        img: "/images/boardimg/1.webp",
        desc: `    10  
                 20   
                ,    2L   
                    &  `,
    },
    {
        title: "2    +  ",
        img: "/images/boardimg/.webp",
        desc: `: + / :  / :   
                 30   or    
                 1    
                  `,
    },
    {
        title: "3   +   ",
        img: "/images/boardimg/.webp",
        desc: ` +    (20)  
                      
                 , 7     
                     ()`,
    },
    {
        title: "4   +  ",
        img: "/images/boardimg/.webp",
        desc: `  ,      
                +   30~40  
                   
                   (    !)`,
    },
];

function DietPostModal({ post, closeModal }) {
    const [views, setViews] = useState(post.views - 1);
    const [likes, setLikes] = useState(post.likesCount);
    const [liked, setLiked] = useState(false);
    const [comments, setComments] = useState([]);
    const [commentInput, setCommentInput] = useState("");

    useEffect(() => {
        setViews((prev) => prev + 1);

        const savedComments = localStorage.getItem("diet-post-comments");
        if (savedComments) {
            setComments(JSON.parse(savedComments));
        }
    }, []);

    const handleLike = () => {
        setLiked(!liked);
        setLikes((prev) => (liked ? prev - 1 : prev + 1));
    };

    const addComment = () => {
        if (commentInput.trim() === "") return;
        const newComments = [...comments, { id: Date.now(), text: commentInput }];
        setComments(newComments);
        setCommentInput("");
        localStorage.setItem("diet-post-comments", JSON.stringify(newComments));
    };

    return (
        <div className="diet-modal-fullscreen" onClick={closeModal}>
            <div className="diet-modal-box" onClick={(e) => e.stopPropagation()}>
                <div className="diet-modal-header">
                    <h1 className="diet-modal-title"> 4     3~5kg  !</h1>
                    <button className="diet-modal-close-button" onClick={closeModal}>
                        X
                    </button>
                </div>

                <div
                    style={{
                        padding: "0 20px",
                        fontSize: "0.9rem",
                        color: "#555",
                        marginBottom: "10px",
                    }}
                >
                    <span>
                        : <strong></strong>
                    </span>{" "}
                    | <span>: {new Date().toLocaleDateString()}</span> |{" "}
                    <span>: {views}</span> |{" "}
                    <span style={{ cursor: "pointer" }} onClick={handleLike}>
                         {likes}
                    </span>
                </div>

                <div className="diet-modal-body post-style-body">
                    <p style={{ marginBottom: "30px", fontSize: "1rem" }}>
                              .
                         4        +    .
                    </p>

                    {dietPlans.map((plan, idx) => (
                        <div
                            key={idx}
                            style={{
                                marginBottom: "25px",
                                borderBottom: "1px solid #ddd",
                                paddingBottom: "15px",
                            }}
                        >
                            <h2>{plan.title}</h2>
                            {plan.img && (
                                <img
                                    src={process.env.PUBLIC_URL +  plan.img}
                                    alt={plan.title}
                                    style={{
                                        width: "100%",
                                        maxHeight: "200px",
                                        objectFit: "cover",
                                        marginBottom: "10px",
                                        borderRadius: "8px",
                                    }}
                                />
                            )}
                            <pre className="whitespace-pre-wrap">{plan.desc}</pre>
                        </div>
                    ))}

                    <div
                        style={{
                            marginTop: "30px",
                            background: "#f9f9f9",
                            padding: "15px",
                            borderRadius: "10px",
                        }}
                    >
                         <strong>TIP:</strong>
                        <br />
                            .
                        4    !
                    </div>

                    <div style={{ marginTop: "30px" }}>
                        <h3> </h3>
                        <input
                            type="text"
                            placeholder=" "
                            value={commentInput}
                            onChange={(e) => setCommentInput(e.target.value)}
                            onKeyDown={(e) => {
                                if (e.key === "Enter") {
                                    addComment();
                                }
                            }}
                            style={{ width: "80%", marginRight: "10px", padding: "8px" }}
                        />
                        <button onClick={addComment}> </button>
                        <ul style={{ marginTop: "15px", paddingLeft: "20px" }}>
                            {comments.map((c) => (
                                <li key={c.id}>- {c.text}</li>
                            ))}
                        </ul>
                    </div>
                </div>
            </div>
        </div>
    );
}

export default DietPostModal;