import { useState, useEffect } from "react";


const teamConditions = [
    {
        title: "1.     ",
        img: "/images/boardimg/.webp",
        desc: "         .   ,   OK!",
    },
    {
        title: "2.  ()",
        img: "/images/boardimg/.webp",
        desc: "             !",
    },
    {
        title: "3.   (  )",
        img: "/images/boardimg/.webp",
        desc: "  ,           !",
    },
    {
        title: "4.   ",
        img: "/images/boardimg/.webp",
        desc: "         .      !",
    },
];

function TeamPostModal({ post, closeModal }) {
    const [views, setViews] = useState(post.views - 1);
    const [likes, setLikes] = useState(post.likesCount);
    const [liked, setLiked] = useState(false);
    const [comments, setComments] = useState([]);
    const [commentInput, setCommentInput] = useState("");

    useEffect(() => {
        setViews((prev) => prev + 1);

        const savedComments = localStorage.getItem("team-post-comments");
        if (savedComments) {
            setComments(JSON.parse(savedComments));
        }
    }, []);

    const handleLike = () => {
        setLiked(!liked);
        setLikes((prev) => (liked ? prev - 1 : prev + 1));
    };

    const addComment = () => {
        if (commentInput.trim() === "") return;
        const newComments = [...comments, { id: Date.now(), text: commentInput }];
        setComments(newComments);
        setCommentInput("");
        localStorage.setItem("team-post-comments", JSON.stringify(newComments));
    };

    return (
        <div className="team-modal-fullscreen" onClick={closeModal}>
            <div className="team-modal-box" onClick={(e) => e.stopPropagation()}>
                <div className="team-modal-header">
                    <h1 className="team-modal-title">BOAT FC   !</h1>
                    <button className="team-modal-close-button" onClick={closeModal}>
                        X
                    </button>
                </div>

                <div
                    style={{
                        padding: "0 20px",
                        fontSize: "0.9rem",
                        color: "#555",
                        marginBottom: "10px",
                    }}
                >
                    <span>
                        : <strong>BOATFC</strong>
                    </span>{" "}
                    | <span>: {new Date().toLocaleDateString()}</span> |{" "}
                    <span>: {views}</span> |{" "}
                    <span style={{ cursor: "pointer" }} onClick={handleLike}>
                         {likes}
                    </span>
                </div>

                <div className="team-modal-body post-style-body">
                    <p style={{ marginBottom: "30px", fontSize: "1rem" }}>
                        BOAT FC       . 
                         20        !
                           !
                    </p>

                    {teamConditions.map((cond, idx) => (
                        <div
                            key={idx}
                            style={{
                                marginBottom: "25px",
                                borderBottom: "1px solid #ddd",
                                paddingBottom: "15px",
                            }}
                        >
                            <h2>{cond.title}</h2>
                            {cond.img && (
                                <img
                                    src={process.env.PUBLIC_URL +  cond.img}
                                    alt={cond.title}
                                    style={{
                                        width: "100%",
                                        maxHeight: "200px",
                                        objectFit: "cover",
                                        marginBottom: "10px",
                                        borderRadius: "8px",
                                    }}
                                />
                            )}
                            <p>{cond.desc}</p>
                        </div>
                    ))}

                    <div
                        style={{
                            marginTop: "30px",
                            background: "#f9f9f9",
                            padding: "15px",
                            borderRadius: "10px",
                        }}
                    >
                        <strong> :</strong>
                        <br />
                            ! 20     
                            !
                    </div>

                    <div style={{ marginTop: "30px" }}>
                        <h3> </h3>
                        <input
                            type="text"
                            placeholder=" "
                            value={commentInput}
                            onChange={(e) => setCommentInput(e.target.value)}
                            onKeyDown={(e) => {
                                if (e.key === "Enter") {
                                    addComment();
                                }
                            }}
                            style={{ width: "80%", marginRight: "10px", padding: "8px" }}
                        />
                        <button onClick={addComment}> </button>
                        <ul style={{ marginTop: "15px", paddingLeft: "20px" }}>
                            {comments.map((c) => (
                                <li key={c.id}>- {c.text}</li>
                            ))}
                        </ul>
                    </div>
                </div>
            </div>
        </div>
    );
}

export default TeamPostModal;