import { useEffect, useState } from 'react';
import './AccountRecoveryModal.css';
import Swal from 'sweetalert2';
import 'sweetalert2/dist/sweetalert2.min.css';

function AccountRecoveryModal({ setOpenModal, accountData, setAccountData }) {

    const [isAnimating, setIsAnimating] = useState(false); //  

    //Email Pannel
    const [userName, setUserName] = useState('');
    const [userPhoneCheck, setUserPhoneCheck] = useState('');


    //Password Pannel
    const [userEmailPass, setUserEmailPass] = useState('');
    const [userPhoneCheckPass, setUserPhoneCheckPass] = useState('');

    // Pannel
    const [user, setUser] = useState({});
    const [userAuthCheck, setUserAuthCheck] = useState('');

    const handleEmailSearch = () => {


        const findUser = (key, value) => {
            return accountData.find(user => user[key] === value);
        };

        const foundUser = findUser('name', userName);
        setUser(foundUser);

        if (!user) {
            Swal.fire({
                icon: 'error',
                title: '   .',
                text: '   .',
            });
        } else if (userAuthCheck !== '1234') {
            Swal.fire({
                icon: 'error',
                title: '   .',
                text: '    .',
            });
        } else {
            Swal.fire({
                icon: 'success',
                title: ' Email ',
                text: foundUser.email,
            });
            setShowTimer(false);
            setUserName('');
            setUserPhoneCheck('');
            setUserAuthCheck('');
        }
    };

    const handleClose = () => {
        setIsAnimating(true); //  
        setTimeout(() => {
            setOpenModal(false); //     
        }, 600); //    
    };

    // ===================================== input    =======================================
    const [newPassword, setNewPassword] = useState('');
    const [newPasswordCon, setNewPasswordCon] = useState('');

    const signUpPassCon = () => {

        if (newPasswordCon == '' || newPasswordCon == '') {
            Swal.fire({
                icon: 'error',
                title: ' ',
                text: '  .',
            });
            return;
        } else if (emailCheckPass == false) {
            Swal.fire({
                icon: 'error',
                title: '  ',
                text: '    .',
            });
            return;
        } else if (authCheck == false) {
            Swal.fire({
                icon: 'error',
                title: ' ',
                text: '    .',
            });
            return;
        }


        const hasSpecialChar = /[!@#$%^&*?]/.test(newPassword);
        if (newPassword.length >= 8 && newPassword.length <= 12) {

            if (!hasSpecialChar) {
                Swal.fire({
                    icon: 'error',
                    title: ' ',
                    text: '8~12  (!@#$%^&*?)  .',
                });
                setNewPassword('');
                setNewPasswordCon('');
            } else if (!(newPassword == newPasswordCon)) {
                Swal.fire({
                    icon: 'error',
                    title: ' ',
                    text: '2   .  .',
                });
                setNewPassword('');
                setNewPasswordCon('');
            } else if (authCheck == false) {
                Swal.fire({
                    icon: 'error',
                    title: ' ',
                    text: '     .',
                });
            } else {

                //    
                const updatedData = accountData.map(user => {
                    if (user.email === userEmailPass) {
                        return {
                            ...user,
                            password: newPassword,
                        };
                    }
                    return user;
                });

                setAccountData(updatedData);

                Swal.fire({
                    icon: 'success',
                    title: 'Password .',
                    text: '     .',
                }).then((result) => {
                    if (result.isConfirmed) {
                        setOpenModal(false);
                    }
                });
                setEmailCheckPass(false);
                setAuthCheck(false);

            }
        }
    }

    // =====================================    =======================================

    const [showTimer, setShowTimer] = useState(false);
    const [showTimerPass, setShowTimerPass] = useState(false);

    //=============  function ==============
    function Timer({ duration }) {
        const [timeLeft, setTimeLeft] = useState(duration);

        useEffect(() => {
            if (timeLeft <= 0) {
                return;
            }

            const interval = setInterval(() => {
                setTimeLeft(prev => {
                    if (prev <= 1) { //1   --
                        clearInterval(interval); // ?
                        return 0;
                    }
                    return prev - 1;
                });
            }, 1000);

            return () => clearInterval(interval);
        }, []);

        //=============    ':'   ==============
        const formatTime = (seconds) => {
            const m = String(Math.floor(seconds / 60)).padStart(2, '0');
            const s = String(seconds % 60).padStart(2, '0');
            return `${m}:${s}`;
        };

        //=============  ==============
        if (timeLeft <= 0) {
            alert('   .');
            return null;
        }
        return <span style={{ marginLeft: '10px', color: 'red' }}>{formatTime(timeLeft)}</span>;
    }


    //===============Email    ====================
    const handleGetCode = () => {
        const findUser = (key, value) => {
            return accountData.find(user => user[key] === value);
        };

        const foundUser = findUser('name', userName);

        if (!userPhoneCheck) {
            Swal.fire({
                icon: 'error',
                title: ' ',
                text: ' .',
            });
            return;
        } else if (!foundUser) {
            Swal.fire({
                icon: 'error',
                title: '  ',
                text: '  .    .',
            });
            return;
        } else if (foundUser.phone !== userPhoneCheck) {
            Swal.fire({
                icon: 'error',
                title: ' ',
                text: '  .',
            });
            return;
        } else {
            Swal.fire({
                icon: 'success',
                title: ' .',
                text: '    ',
            })
        }

        setShowTimer(true); //  
    };


    //===============Password    ====================
    const [emailCheckPass, setEmailCheckPass] = useState(false);

    const userEmailCheck = () => {
        const foundUserEmail = accountData.find(user =>
            user.email === userEmailPass);

        if (foundUserEmail) {
            Swal.fire({
                icon: 'success',
                title: '  .',
                text: '  .',
            })
            setEmailCheckPass(true);
            return;
        } else {
            Swal.fire({
                icon: 'error',
                title: '   .',
                text: '  .',
            })
            return;
        }
    }


    const handleGetCodePass = () => {

        const foundUserPhone = accountData.find(user =>
            user.email === userEmailPass && user.phone === userPhoneCheckPass);


        if (userPhoneCheckPass == '') {
            Swal.fire({
                icon: 'error',
                title: ' ',
                text: ' .',
            });
            return;
        } else if (!foundUserPhone) {
            Swal.fire({
                icon: 'error',
                title: ' ',
                text: '  .',
            });
            return;
        } else {
            Swal.fire({
                icon: 'success',
                title: ' .',
                text: '    ',
            })
        }

        setShowTimerPass(true); //  
    };

    //Password   =====================================================
    const [authCheckPass, setAuthCheckPass] = useState('');
    const [authCheck, setAuthCheck] = useState(false);

    const authCheckTF = () => {
        if (authCheckPass == '1234') {
            setShowTimerPass(false);
            setAuthCheck(true);
            Swal.fire({
                icon: 'success',
                title: ' .',
                text: '  .',
            })
            return;
        } else {
            Swal.fire({
                icon: 'error',
                title: '  .',
                text: '   .',
            })
        }
    }


    return (
        <div className='account-all'>
            <div className={`account-re-body account-re-div ${isAnimating ? 'fade-out' : ''}`}>
                <div className='account-re-wrapper account-re-div'>
                    <div className='account-re-container'>
                        <div className='account-re-bg'>
                            <div className='account-re-close' onClick={handleClose}>X</div>
                            {/*================== Email ================= */}
                            <div className='account-re-bg-top'>
                                <div className='account-re-email-container account-re-div'>

                                    <form className='account-re-form'>
                                        <h1 className='account-re-h1 account-re-email-h1 account-re-div'>Email </h1>
                                        <input
                                            type='text'
                                            placeholder=''
                                            className='account-re-input'
                                            value={userName}
                                            onChange={(e) => setUserName(e.target.value)}
                                        ></input>

                                        <div className='account-re-div'>


                                            <input
                                                type='text'
                                                placeholder=''
                                                className='account-re-input'
                                                value={userPhoneCheck}
                                                onChange={(e) => setUserPhoneCheck(e.target.value)}
                                                maxLength={11}

                                            ></input>{showTimer && <Timer duration={180} />}

                                            <button type='button' className='account-re-btn' onClick={handleGetCode}> </button>
                                        </div>

                                        <div className='account-re-div'>
                                            <input
                                                type='text'
                                                placeholder=''
                                                className='account-re-input'
                                                value={userAuthCheck}
                                                onChange={(e) => setUserAuthCheck(e.target.value)}
                                            ></input>
                                        </div>

                                        <div className='account-re-div'>
                                            <button type='button' className='account-re-sub-btn account-re-emailsub-btn' onClick={handleEmailSearch}>Email </button>
                                        </div>

                                    </form>
                                </div>

                                {/*================== Password ================= */}
                                <div className='account-re-pass-container account-re-div'>
                                    <form className='account-re-form account-re-div'>
                                        <h1 className='account-re-h1'>Password </h1>
                                        <div className='account-re-div'>
                                            <input
                                                type='email'
                                                placeholder='email'
                                                className='account-re-input'
                                                value={userEmailPass}
                                                onChange={(e) => setUserEmailPass(e.target.value)}
                                            ></input>
                                            <button type='button' className='account-re-btn' onClick={userEmailCheck}> </button>
                                        </div>

                                        <div className='account-re-div'>
                                            <input
                                                type='text'
                                                placeholder=''
                                                className='account-re-input'
                                                value={userPhoneCheckPass}
                                                onChange={(e) => setUserPhoneCheckPass(e.target.value)}
                                                maxLength={11}
                                            ></input>{showTimerPass && <Timer duration={180} />}

                                            <button type='button' className='account-re-btn' onClick={handleGetCodePass}> </button>
                                        </div>

                                        <div className='account-re-div'>
                                            <input
                                                type='text'
                                                placeholder=''
                                                className='account-re-input'
                                                value={authCheckPass}
                                                onChange={(e) => setAuthCheckPass(e.target.value)}
                                            ></input>
                                            <button type='button' className='account-re-btn' onClick={authCheckTF}></button>
                                        </div>

                                        <div className='account-re-div'>
                                            <input
                                                type='password'
                                                placeholder=' (8 - 12 )'
                                                className='account-re-input'
                                                value={newPassword}
                                                onChange={(e) => setNewPassword(e.target.value)}
                                                minLength={8}
                                                maxLength={12}
                                            ></input>
                                        </div>

                                        <div className='account-re-div'>
                                            <input
                                                type='password'
                                                placeholder='  2 (8 - 12 )'
                                                className='account-re-input'
                                                value={newPasswordCon}
                                                onChange={(e) => setNewPasswordCon(e.target.value)}
                                                minLength={8}
                                                maxLength={12}
                                            ></input>
                                        </div>

                                        <div className='account-re-div'>
                                            <button type='button' className='account-re-sub-btn' onClick={signUpPassCon} >Password </button>
                                        </div>

                                    </form>
                                </div>
                            </div>
                        </div>
                    </div>
                </div>
            </div>
        </div>
    );

}

export default AccountRecoveryModal;